import React from 'react';
import styled from 'styled-components';
import blunderbuss from './blunderbuss.png';
import { useHistory } from 'react-router-dom';
import { gsap } from 'gsap';

const Wrap = styled.div`
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  display: grid;
  grid-template-columns: repeat(2, 1fr);

  @media screen and (orientation: portrait) {
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: repeat(2, 1fr);
  }
`;

const HomeWrapper = styled.div`
  width: 50vw;
  height: 90vh;
  overflow: hidden;
  transform-style: preserve-3d;
  transform: rotate(25deg) skew(-25deg);
  margin-top: 15vh;

  @media screen and (orientation: portrait) {
    transform-style: none;

    img {
      transform: scale(0);
    }
  }

  img {
    width: 50vh;
    height: 50vh;
    transform: translateY(30vh);
    margin-left: 10vw;
    opacity : 1;
  }

  h1 {
    font-size: 4vw;
    text-align: center;
  }
  button {
    width: 20vw;
    height: 5vw;
    margin-left: 15vw;
    font-size: 2.4vw;
    background-color: #b33b3b;
    color: white;
    border: none;
    cursor: pointer;

    :hover {
      border-bottom: solid #993232 0.3vw;
      border-right: solid #993232 0.3vw;
    }
  }
`;

const Home = () => {
  const history = useHistory();
  return (
    <Wrap>
      <HomeWrapper>
        <h1>Powstania i Spiski</h1>
        <button
          onClick={() => {
            history.push('/his2//select/');
          }}
        >
          Dalej
        </button>
      </HomeWrapper>
      <HomeWrapper>
        <img src={blunderbuss} />
      </HomeWrapper>
    </Wrap>
  );
};

export default Home;
