import React from 'react';
import logo from './logo.svg';
import './App.css';
import Isocard from './components/Isocard';
import styled from 'styled-components';
import bg1 from './components/bg1.jpg';
import bg2 from './components/bg2.jpg';
import { useHistory } from 'react-router-dom';
import { gsap } from 'gsap';

const AppWrapper = styled.div`
  width: 60vw;
  height: 60vh;
  overflow: hidden;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(1, 1fr);
  align-items: center;
  justify-items: center;
  transform-style: preserve-3d;
  transform: rotate(25deg) skew(-25deg);
  margin: auto auto;
  margin-top: 20vh;
  opacity : 0;
`;

function Select() {
  const history = useHistory();
  const ref1 = React.useRef(null);

  React.useEffect(() => {
    const tl = gsap.timeline();
    const wrapper = ref1.current;

    tl.to(wrapper,0.6,{ opacity : 1 });
  });
  return (
    <AppWrapper ref={ref1}>
      <Isocard
        nextFunc={() => history.push('/his2//select/powls')}
        title='Powstanie Listopadowe'
        img={bg1}
      ></Isocard>
      <Isocard
        nextFunc={() => history.push('/his2//select/powst')}
        title='Powstanie Styczniowe'
        img={bg2}
      ></Isocard>
    </AppWrapper>
  );
}

export default Select;
