import React from 'react';
import logo from './logo.svg';
import './App.css';
import Select from './Select';
import Home from './components/Home';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import Powls from './components/Powls';
import Powst from './components/Powst';

function App() {
  return (
    <Router>
      <Switch>
        <Route exact path='/his2/' component={Home} />
        <Route exact path='/his2//select/' component={Select} />
        <Route exact path='/his2//select/powls' component={Powls} />
        <Route exact path='/his2//select/powst' component={Powst} />
      </Switch>
    </Router>
  );
}

export default App;
