import React from 'react';
import Article from './Article';
import styled from 'styled-components';
import AR from './st.jpg';
import Starr from './starr';
import { gsap } from 'gsap';

const Wrap = styled.div`
  background-image: url(${AR});
  background-size: cover;
  background-attachment: fixed;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-template-rows: repeat(3, 1fr);
  overflow: hidden;
  align-items : center;
  justify-items: center;
  grid-gap: 15vh ;
  opacity : 0;
`;

const Powst = () => {
    const ref1 = React.useRef(null);

    React.useEffect(() => {
      const tl = gsap.timeline();
      const wrapper = ref1.current;
  
      tl.to(wrapper,0.6,{ opacity : 1 });
    });
  return (
    <Wrap ref={ref1}>
      {
          Starr.map(item => (
              <Article key={item.id} title={item.title} art={item.art} bgg={item.bgg} />
          ))
      }
    </Wrap>
  );
};

export default Powst;
