import React from 'react';
import styled from 'styled-components';

const Card = styled.div`
  width: 20vw;
  height: 50vh;
  position: relative;
  cursor : pointer;

  span:nth-child(1) {
    background: linear-gradient(rgba(0, 0, 0, 0.25), rgba(0, 0, 0, 0.25)),
      url(${({ bg }) => bg});
    background-size: 50vw;
    filter: grayscale(0.6);
    z-index: 10;

    h1 {
      color: white;
      font-size: 1vw;
      display: flex;
      justify-content: flex-end;
      align-self: center;
      height: 5vh;
      margin-right: 5%;
      transform: translateY(45vh);
    }
  }
  span:nth-child(2) {
    background-color: #b33b3b;
    z-index: 9;
  }
  span:nth-child(3) {
    background-color: #993232;
    z-index: 8;
  }
  span:nth-child(4) {
    background-color: #8c2e2e;
    z-index: 7;
  }

  :hover {
    span:nth-child(1) {
      transform: translate(-45px, -45px);
    }
    span:nth-child(2) {
      transform: translate(-35px, -35px);
    }
    span:nth-child(3) {
      transform: translate(-25px, -25px);
    }
    span:nth-child(4) {
      transform: translate(-15px, -15px);
    }
  }

  span {
    width: 20vw;
    height: 50vh;
    position: absolute;
    transform-style: preserve-3d;
    transition: 0.6s;
  }
`;

const Isocard = ({ img, title, nextFunc }) => {
  return (
    <Card onClick={nextFunc} bg={img}>
      <span height='40px'>
        <h1>{title}</h1>
      </span>
      <span height='30px' />
      <span height='20px' />
      <span height='10px' />
    </Card>
  );
};

export default Isocard;
