import React from 'react';
import styled from 'styled-components';
import bgg1 from './t3.png';

const Art = styled.div`
  width: 90vw;
  height: 90vh;
  background-image: url(${({ bg }) => bg});
  background-size: cover;
  background-color: whitesmoke;
  margin: auto;
  transform: translateY(5vh);

  h1 {
    font-size: 3vw;
    margin: 0;
    padding: 2vw;
    padding-bottom: 0;
  }
  article {
    text-align: justify;
    font-size: 1.02vw;
    max-width: 34vw;
    padding-left: 2vw;
    padding-top: 5vh;
  }
`;

const Article = ({ bgg, title, art }) => {
  return (
    <Art bg={bgg}>
      <h1>{title}</h1>
      <article>{art}</article>
    </Art>
  );
};

export default Article;
